$font-family: 'SF Pro Text', Sans-Serif;
$font-title: 'Bebas Kai', Sans-Serif;
$color-main: #5596cc;
$color-main: #5596cc;
$color-white: #fdfdfd;
$color-black: #111;
$header-height: 70px;
$color-bg: #fdfdfd;
$color-font: #222;
$footer-height: 2rem;
$max-width: 1200px;
:root{
  --color-main: #5596cc;
  --color-white: #fdfdfd;
  --color-black: #111;
  --header-height: 70px;
  --footer-height: 2rem;
  --color-bg: #fdfdfd;
  --color-font: #222;
  --z-normal: 1;
  --z-fixed: 100;
  --z-modal: 1000;
  --time: 1s;
  --font-main: 'SF Pro Text', Sans-Serif;
  --font-title: 'Bebas Kai', Sans-Serif;
  --font-size: 16px;
  --line-height: 1.5;
  --flight: 300;
  --fNormal: 400;
  --fBold: 700;

  @media screen and(min-width: 1024px){
    --font-size: 1rem;
    --font-lh: 1.4rem;
  }
}

*, *::before, *::after {
  box-sizing: border-box;
}

[class^="icon-"]:before{
  position: relative;
  top: 2px;
}

img{
  max-width: 100%;
  height: auto;
  display: inline-block;
}

a{
  display: inline-block;
  text-decoration: none;
  color: inherit;
  &.hover{
    text-decoration: none;
  }
}
html, body{
  background: $color-bg;
  margin: 0;
}

body{
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

h1, h2, h3, h4, h5, h6, p, figure, section, article{
  margin: 0;
  padding: 0;
}
%list-style, ul{
  list-style: none;
  padding: 0;
  margin: 0;
}
.fs-xsmall{
  font-size: calc(var(--font-size) * .7);
  line-height: calc(var(--font-lh) * .7);
}

.fs-small{
  font-size: calc(var(--font-size) * .9);
  line-height: calc(var(--font-lh) * .9);
}


.fs-big{
  font-size: calc(var(--font-size) * 1.5);
  line-height: calc(var(--font-lh) * 1.5);
}

.fs-xbig{
  font-size: calc(var(--font-size) * 2);
  line-height: calc(var(--font-lh) * 2);
}

body{
  font-size: var(--font-size) ;
  line-height: var(--font-lh);
  font-family: var(--font-main);
  font-weight: var(--fNormal);
  color: var(--color-font);
  background: var(--color-bg);
}

h1,h2,h3,h4,h5,h6{
  font-weight: var(--fBold);
  font-family: var(--font-title);
}

h1{
  --font-size: 2rem;
  --font-lh: 2.2rem;
}

h2{
  --font-size: 1.8rem;
  --font-lh: 2rem;
}

h3{
  --font-size: 1.6rem;
  --font-lh: 1.8rem;
}

h4{
  --font-size: 1.4rem;
  --font-lh: 1.6rem;
}

h5{
  --font-size: 1.4rem;
  --font-lh: 1.6rem;
}

body.login{
  $lg: rgba(#000,.3);
  background: linear-gradient($lg, $lg),
              url(../assets/img/5.jpg) center / cover no-repeat;
  justify-content: center;
  align-items: center;
  overflow-x: hidden;
  .logo{
    display: block;
    width: 60%;
    max-width: 300px;
    margin: 0 auto 4em;
  }
  .footer-form{
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 1em 0 0;
    p, button{
      margin-bottom: 1em;
    }
  }
}

.login__container{
  width: 80%;
  max-width: 450px;
  height: auto;
  max-height: 80vh;
  background: rgba(#eeeeee,.85);
  padding: 4rem;
  box-shadow: 0px 0px 50px rgba(#000,.8);
  border-radius: .3rem;
}
.main-header{
  height: 75px;
}
.container{
  margin: 0 auto;
  max-width: $max-width;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  img{
    width: auto;
    height: 80%;
    display: block;
  }
}
.main{
  margin-top: 2rem;
  .container{
    display: block;
  }
}
.margin-bottom{
  margin-bottom: 2rem;
}
.button-container{
  display: flex;
  justify-content: flex-end;
  margin-bottom: 2em;
}
.item{
  text-align: center;
  p, label{
    margin-bottom: .5em;
    color: var(--color-font) !important;
    // text-align: center;
    // background: #5596cc;    
  }
  p:first-of-type{
    font-weight: var(--fBold);
  }
}
.item-menu{
  width: 60%;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  li{
    margin-right: 1em;
    &:last-child{
      margin-right: 0;
    }
  }
}
.grid-container{
  display: grid;
  grid-template-columns: repeat(4,1fr);
  grid-gap: 2rem;
}
.btn{
  $size: 2em;
  display: block;
  border: none;
  width: $size;
  height: $size;
  line-height: 2em;
  color: $color-bg;
  cursor: pointer;
  border: .3em;
  &-circle{
    border-radius: $size;
    margin: 0 auto;
    display: block;
  }
  &-primary{
    background: $color-main;
  }
  &-danger{
    background: rgba(218, 40, 8, 0.781);
  }
  &-close, &-cancel, &-save{
    width: auto;
    height: auto;
    padding: 0 1em;
    line-height: 3em;
    &:before{
      margin-right: .5em;
    }
  }
  &-cancel{
    color: $color-font
  }
  &-save{
    background: $color-main;
  }
}

// .table-promos{

// }
.table-container{
  overflow-x: auto;
}
.promos{
  width: $max-width;
  border: none;
  border-collapse: collapse;
  // overflow-x: auto;
  td{
    text-align: center;
  }
  thead{
    background: var(--color-main);
    color: #fff;
  }
  th, td{
    line-height: 3em;
    text-transform: uppercase;
  }
  tr:hover{
    background: rgba(#ccc,.3);
  }
  tbody{
    tr:nth-child(even){
      background: rgba(#ccc,.2);
    }
    tr:hover{
      background: rgba($color-main,.1);
    }
  }
}

.modal{
  display: none;
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(#000,.75);
  opacity: 0;
  justify-content: center;
  align-items: center;
  &.show{
    display: flex;
    animation: modal .3s forwards ;
    @keyframes modal {
      to{
        opacity: 1;
      }
    }
    .modal__container{
      padding: 3em;
      max-height: initial;
    }
  }
  &__close{
    cursor: pointer;
    font-size: 4em;
    position: absolute;
    right: 2rem;
    top: 2rem;
    color: #fff;
  }
  &__container{
    max-width: 750px;
    width: 90%;
    // display: table;
    background: $color-bg;
    width: auto;
    // padding: 3em;
    max-height: 0;
    overflow: hidden;
    transition: .5s .3s max-height;
  }
}
.input-group{
  $lh: 2.5em;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 200px;
  grid-gap: 1em;
  margin-bottom: 1em;
  textarea{
    height: 5em;
    resize: none;
    
  }
  input, textarea, select{
    border: 1px solid rgba(#ccc,.8);
    color: $color-font;
    font-size: .9em;
    font-family: $font-family;
    padding: .5em;
  }
  select{
    height: $lh;
  }
  .label-lh{
    line-height: $lh;
  }
}
.footer-form{
  display: flex;
  justify-content: flex-end;
  button{
    margin-right: 1em;
    &:last-child{
      margin-right: 0;
    }
  }
}
.msg{
  display: none;
  opacity: 0;
  transition: opacity .5s linear;
  &.show{
    display: block;
    opacity: 1;
  }
}
.modal-loading{
  background: $color-bg;
  opacity: 1;
  transition: .5s;
  img{
    display: block;
    width: 150px;
    height: auto;
  }
}
